
.navbar{
    position: fixed;
    display: flex;
    justify-content: space-between ;
    align-items: center;
    z-index: 2;
    background-color: var(--primary3);
    top:0;
    left:0;
    right:0;
    box-shadow: 0px 5.333px 80px 0 rgba(0, 0, 0, 0.1);
    padding-top: 20px;
}
.navbar-items>ul{
    list-style: none;
    display: flex;
    align-items: flex-start;
    gap: 46px;

}
.navbar-items>ul>li{
    text-decoration: none;
    cursor: pointer;
    list-style: none;
}
a:link{
     color:black;
     text-decoration: none;
 }
a:visited{
    color:black;
    text-decoration: none;
}

.navbar-content {
    color: black;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    cursor: pointer;
}
.navbar-active-content {
    color: black;
}


/*Navbar mobil version*/
.nav-hamburger {
    display: none;
    width: 1.875rem;
    height: 1.313rem;
    flex-direction: column;
    justify-content: space-around;
    position: absolute;
    top: 25px;
    right: 25px;
}

.nav-hamburger-line {
    display: block;
    height: 0.188rem;
    width: 100%;
    background-color: #000000;
    border-radius: 0.625rem;
    transition: all ease-in-out 0.2s;
}

@media screen and (max-width: 1200px) {
    .nav-hamburger {
        display: flex;
        z-index: 200;
    }
    .navbar-items {
        display: flex;
    }
    .navbar-items {
        position: absolute;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgb(234, 222, 222);
        top: -40rem;

        left: 0;
        width: 100%;
        transition: all ease-in-out 0.4s;
        cursor: pointer;
    }
    .navbar-items ul {
        flex-direction: column;
        align-items: center;
        width: 100%;
        padding: 3rem 0 0.6rem;
        z-index: -100;
    }
    .navbar-items ul li {
        text-align: center;
    }
    .navbar-items ul li a {
        padding: 0.5rem;
    }
    .navbar-items {
        z-index: -1000;
    }
    .navbar-items.active {
        top: 70px;
    }
    .nav-hamburger.active :nth-child(1) {
        transform: rotate(45deg) translate(0.45rem, 0.1875rem);
    }

    .nav-hamburger.active :nth-child(2) {
        opacity: 0;
    }

    .nav-hamburger.active :nth-child(3) {
        transform: rotate(-45deg) translate(0.45rem, -0.1875rem);
    }
}